<template>
  <div class="markdown-body" v-html="htmlContent">
  </div>
</template>

<script>
import { getMarkdownContent } from '../utils/request'
import { ref, onMounted } from 'vue'

const htmlContent = ref('')

export default {
  name: 'About',
  setup () {
    onMounted(async () => {
      const result = await getMarkdownContent('repos/partyyaya/GitLearning/contents/README.md')
      htmlContent.value = result
    })
    return { htmlContent }
  }
}
</script>
<style lang="scss">
@import "../style/md.scss";
</style>
